.wrapper {
  padding: 30px 10px 50px 10px;
  /* margin: 0 20px; */
  margin: 0 auto;
  width: 320px;
  background: url('../../../img/background/debtorsMob.jpg');
  background-image: -webkit-image-set(url('../../../img/background/debtorsMob.webp') 1x, url('../../../img/background/debtorsMob.jp2') 1x, url('../../../img/background/debtorsMob.jpg') 1x);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
}
.header {
  font-family: 'Caviar Dreams';
  font-size: 22px;
  line-height: 36px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: 300;
  margin: 0 0 15px 0;
}
.text {
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
  width: 300px;
  margin: 0 auto 15px auto;
}
.smallImg {
  display: inline-block;
  width: 60px;
  height: auto;
}
.smallImg:not(:last-child) {
  margin-right: 15px;
}
.debtorsRegister {
    display: block;
    width: 300px;
    height: auto;
    margin: 0 auto;
}
.imgWrapper {
  width: 300px;
  margin: 0 auto 40px auto;
}

@media(min-width: 1200px) {
  .wrapper {
    /* margin: 0 70px; */
    margin: 0 auto;
    width: 1170px;
    padding: 30px 50px 220px 50px;
    background: url('../../../img/background/debtors.png');
    background-image: -webkit-image-set(url('../../../img/background/debtors.webp') 1x, url('../../../img/background/debtors.jp2') 1x, url('../../../img/background/debtors.png') 1x);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .header {
    font-size: 48px;
    line-height: 60px;
    margin-bottom: 50px;
  }
  .text {
    font-size: 18px;
    line-height: 30px;
    width: 40%;
    margin: 0 0 30px 0;
  }
  .smallImg {
    width: 120px;
  }
  .smallImg:not(:last-child) {
    margin-right: 30px;
  }
  .imgWrapper {
    margin: 0;
    width: unset;
  }
}
